.hero {
  box-shadow: 5px 10px 10px var(--gray-300);

}

.profile {

  &__bg {
    @include size(100%, 400px);

    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
  }

  &__tagline {
    @include size(300px, auto);
    @include font-size(24px);

    font-weight: var(--bold);
    color: var(--white);
    text-align: center;
    position: absolute;
    bottom: 25px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }

  &__block {
    background-size: cover;
    background-repeat: no-repeat;

    &:after {
      content: '';
      height: 3px;
      width: 100%;
      background-color: var(--gray-200);
    }
  }

  &__user {
    @include size(100%, auto);

    background-color: var(--white);
    border-bottom: 3px solid var(--blue);
  }

  .user {
    position: relative;

    &__block {
      @include flex;
    }

    &__img {
      @include size(125px);
      @include make-spacer(mr, 4);

      position: absolute;

      img {
        @include size(100%);

        border: 3px solid var(--blue);
        border-radius: 15px;
        margin-top: -62.5px;
      }
    }

    &__industry {
      //display: grid;
      //grid-template-columns: 72px 170px 1fr;
      @include flex(center, center);
      //@include make-spacer(py, 1);

      margin-left: 140px;

    }

    &__creds {
      @include flex(center, center, row);

      .creds {
        @include flex(center, center, column);
        @include make-spacer(px, 3);

        border-right: 1px solid gray;
        height: 100%;

        &:first-child {
          border-left: 1px solid gray;
        }

        .icon {
          @include size(35px);
          @include make-spacer(my, half);
        }

        .count {
          @include font-size(12px);

          color: var(--red);
          font-weight: var(--bold);
        }

        .title {
          @include font-size(7px);

          text-transform: uppercase;
          font-weight: var(--bold);
        }
      }
    }

    &__avatar {
      @include size(52px);

      border: 2px solid var(--blue-100);
      border-radius: 10px;

      img {
        @include size(100%);

        border-radius: 8px;
      }
    }

    &__name {

      .name {
        font-weight: bold;
      }
    }
  }

  .industry {

    &__icon {
      @include flex(center, center);
      @include make-spacer(py, 4);
      @include make-spacer(px, 4);

      background-color: var(--blue);
      border-radius: 10px;

      .icon {
        @include size(40px);
      }
    }

    &__title {
      @include flex(center, center);
      @include make-spacer(pl, 4);
      @include make-spacer(pr, 2);
      @include size(180px, 100%);
      @include font-size(15px);

      color: var(--gray-light);
      font-family: 'DM Mono', monospace;
    }
  }

  &__data {
    @include flex(flex-start, center);
    //@include make-spacer(my, 5);

    .name {
      @include font-size(24px);
      @include make-spacer(mr, 5);

      font-weight: var(--bold);
    }

    .location {
      @include flex(flex-start, center);
      @include font-size(10px);

      text-transform: uppercase;
      font-weight: var(--bold);

      .icon {
        @include size(15px);
        @include make-spacer(mr, 1);
      }
    }
  }

  .user-name {
    @include make-spacer(my, 4);
  }
}
