.header {
  @include size(100%, var(--header-height));
  @include font-size(16px);

  display: grid;
  grid-template-columns: rem(100px) 1fr rem(200px);
  align-items: center;
  z-index: map-get($z-indexes, header);
  background-color: var(--black-secondary);
  font-family: var(--headings-font-family);

  position: fixed;

  &__logo {
    @include font-size(24px);
    @include make-spacer(ml, 20);

    color: var(--yellow);

    .icon {
      @include size(230px, 55px);
    }
  }

  &__controls {
    @include flex;

    .icon {
      @include make-spacer(mr, 10);

      color: var(--gray-10);
    }
  }

  &__language {
    display: none;

    @include media-breakpoint-up(md) {
      @include flex($justify-content: flex-end);
    }
  }

  &__nav {
    display: none;

    @include media-breakpoint-up(md) {
      display: block;
    }
  }

  &__container {
    @include flex(space-between, center);
  }

  &__menu {
    @include flex(center, center);
    @include make-spacer(pt, 1);

    border-left: 1px solid var(--grey-500);

    &.is-disabled {
      pointer-events: none;
    }

    &:focus {
      outline: none;
    }
  }
}
